.Checkbox {
    display: block;
    position: relative;
    padding-left: 4.5rem;
    text-align: left;
    margin-bottom: 1.5rem;
    line-height: 1.5rem;
    cursor: pointer
}

.Checkbox.disabled {
    pointer-events: none;
    opacity: .5
}

.Checkbox.round .Checkbox-main::before {
    border-radius: 50%
}

.Checkbox.round .Checkbox-main::after {
    background: var(--color-primary) url(data:image/svg+xml;base64,) no-repeat 50% 50%;
    background-size: 12px;
    border-radius: 50%
}

.Checkbox.loading .Checkbox-main::before,
.Checkbox.loading .Checkbox-main::after {
    opacity: 0 !important
}

.Checkbox.loading .Spinner {
    position: absolute;
    left: .375rem;
    top: .125rem;
    opacity: 0;
    animation: fade-in .2s ease forwards;
    --spinner-size: 1.25rem
}

.Checkbox.blocking input:not(:checked)~.Checkbox-main::before {
    border-color: var(--color-error)
}

.Checkbox.blocking input:not(:checked)~.Checkbox-main::after {
    background-image: url(data:image/svg+xml;base64,);
    opacity: 1;
    background-size: 1.375rem;
    background-position: -0.125rem -0.125rem
}

.Checkbox input {
    position: absolute;
    z-index: var(--z-below);
    opacity: 0
}

.Checkbox .Checkbox-main::before,
.Checkbox .Checkbox-main::after {
    content: "";
    display: block;
    position: absolute;
    left: 1.1875rem;
    top: .1875rem;
    width: 1.125rem;
    height: 1.125rem
}

.Checkbox .Checkbox-main::before {
    border: 2px solid var(--color-borders-input);
    border-radius: .25rem;
    background-color: var(--color-background);
    transition: border-color .1s ease, background-color .1s ease
}

.Checkbox .Checkbox-main::after {
    background: center no-repeat url(data:image/svg+xml;base64,);
    background-size: .875rem;
    opacity: 0;
    transition: opacity .1s ease
}

.Checkbox .Checkbox-main .label {
    display: block;
    text-align: initial
}

.Checkbox .Checkbox-main .subLabel {
    display: block;
    font-size: .875rem;
    line-height: 1rem;
    color: var(--color-text-secondary)
}

.Checkbox input:checked~.Checkbox-main::before {
    border-color: var(--color-primary);
    background-color: var(--color-primary)
}

.Checkbox input:checked~.Checkbox-main::after {
    opacity: 1
}

.Checkbox[dir=rtl] {
    padding-left: 0;
    padding-right: 4.5rem
}

.Checkbox[dir=rtl].loading .Spinner {
    left: auto;
    right: .375rem
}

.Checkbox[dir=rtl] .label,
.Checkbox[dir=rtl] .subLabel {
    text-align: right
}

.Checkbox[dir=rtl] .Checkbox-main::before,
.Checkbox[dir=rtl] .Checkbox-main::after {
    left: auto;
    right: 1.1875rem
}