.Modal {
    position: relative;
    z-index: var(--z-modal)
}

.Modal.confirm {
    z-index: var(--z-lock-screen)
}

.Modal.delete .modal-dialog,
.Modal.error .modal-dialog,
.Modal.confirm .modal-dialog,
.Modal.pin .modal-dialog,
.Modal.unpin-all .modal-dialog {
    max-width: 23rem
}

.Modal.error .modal-content>div {
    margin-top: 1rem
}

.Modal.narrow .modal-dialog {
    max-width: 20rem
}

.Modal.report .modal-dialog {
    max-width: 15rem
}

.Modal .modal-container {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center
}

.Modal .modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: rgba(0, 0, 0, .25)
}

.Modal.transparent-backdrop .modal-backdrop {
    background-color: rgba(0, 0, 0, 0)
}

.Modal .modal-dialog {
    position: relative;
    display: inline-flex;
    flex-direction: column;
    width: 100%;
    max-width: 35rem;
    min-width: 17.5rem;
    margin: 2rem auto;
    background-color: var(--color-background);
    box-shadow: 0 .25rem .5rem .125rem var(--color-default-shadow);
    border-radius: var(--border-radius-default);
    transform: translate3d(0, -1rem, 0);
    transition: transform .2s ease, opacity .2s ease
}

body.animation-level-0 .Modal .modal-dialog {
    transition: none;
    transform: none !important
}

@media(max-width: 450px) {
    .Modal .modal-dialog {
        max-width: calc(100vw - 3rem) !important
    }
}

.Modal.open .modal-dialog {
    transform: translate3d(0, 0, 0)
}

.Modal.closing .modal-dialog {
    transform: translate3d(0, 1rem, 0)
}

.Modal .modal-header,
.Modal .modal-header-condensed {
    padding: 1rem 1rem 0;
    display: flex;
    align-items: center;
    flex-shrink: 0
}

.Modal .modal-title {
    font-size: 1.25rem;
    font-weight: 500;
    flex: 1 1 auto;
    overflow: hidden;
    text-overflow: ellipsis
}

.Modal .modal-title:not(:only-child) {
    margin: 0 1.3125rem
}

.Modal .modal-header-condensed {
    padding: .5rem 1.25rem 0 .9375rem !important
}

.Modal .modal-header-condensed .modal-action-button {
    font-size: .875rem;
    height: 2.25rem;
    width: auto;
    line-height: 1.625rem;
    margin-left: auto;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    min-width: 5rem
}

.Modal .modal-header-condensed .modal-action-button.danger {
    background-color: var(--color-error);
    color: var(--color-white)
}

.Modal .modal-header-condensed .modal-action-button.danger:hover,
.Modal .modal-header-condensed .modal-action-button.danger:focus,
.Modal .modal-header-condensed .modal-action-button.danger:active {
    background-color: var(--color-error-shade)
}

.Modal .modal-content {
    width: 100%;
    flex-grow: 1;
    padding: 1rem;
    overflow-y: auto;
    max-height: 90vh
}

.Modal .modal-content b,
.Modal .modal-content strong {
    word-break: break-word
}

.Modal .modal-title,
.Modal .modal-content,
.Modal .modal-content>p {
    unicode-bidi: plaintext;
    text-align: initial
}

.Modal .modal-about {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    text-overflow: ellipsis;
    overflow: hidden
}

.Modal .modal-help {
    color: var(--color-text-secondary);
    font-size: .9375rem;
    line-height: 1.3
}

.Modal .dialog-buttons {
    display: flex;
    align-items: flex-end
}

.Modal .dialog-buttons .confirm-dialog-button+.confirm-dialog-button {
    margin-left: 1rem
}

.Modal .confirm-dialog-button {
    width: auto;
    height: auto;
    margin-left: auto;
    text-align: right;
    font-weight: 500;
    white-space: pre-wrap
}