.AvatarEditable label {
    margin-left: auto;
    margin-right: auto;
    width: 7.5rem;
    height: 7.5rem;
    margin-bottom: 2rem;
    display: flex !important;
    align-items: center;
    justify-content: center;
    background-color: var(--color-primary);
    border-radius: 50%;
    color: #fff;
    font-size: 3rem;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    outline: none !important
}

.AvatarEditable label input {
    display: none
}

.AvatarEditable label img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.AvatarEditable label i {
    transform: scale(1);
    transition: transform .15s linear;
    z-index: var(--z-register-add-avatar)
}

.Transition>div:not(.Transition__slide--active) .AvatarEditable label input,
.Transition>div:not(.Transition__slide--active) .AvatarEditable label i,
.Transition>div:not(.Transition__slide--active) .AvatarEditable label::after {
    display: none !important
}

.Transition>div:not(.Transition__slide--active) .AvatarEditable label img {
    position: static !important
}

.AvatarEditable label:hover i {
    transform: scale(1.2)
}

.AvatarEditable label.filled {
    background-color: var(--color-background)
}

.AvatarEditable label.filled::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .4)
}

.AvatarEditable label.disabled {
    pointer-events: none
}

.AvatarEditable label.disabled i {
    display: none
}

.AvatarEditable label.disabled::after {
    display: none
}